@import url(https://fonts.googleapis.com/css?family=Oswald);
@import url(https://fonts.googleapis.com/css?family=Sacramento);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  font-family: 'Oswald', sans-serif;
}

h2,
.communityName{
  font-family: 'Sacramento', cursive;
}

.header{
  background-image: url(/apartmentsite/static/media/header.1bd1f4c1.jpg);
  width: 100vw;
  height: 20em;
}

.communityName{
  padding-top: 100px;
  font-size: 6em;
  font-weight: 900;
  color: #0086b3;
  text-shadow:
  -1px -1px 0 #ccc,  
   1px -1px 0 #ccc,
   -1px 1px 0 #ccc,
    1px 1px 0 #ccc;
}


.navbar-brand{
  font-family: 'Sacramento', cursive;
  font-size: 2em;
}

.community,
.communityDescription{
  padding-top: 15px;
  width: 80vw;
  margin: auto;
  font-size: 1.25em;
  display: flex;
  flex-direction: row;
  align-items: center;
  }

  @media screen and (max-width: 767px){
    .community, .communityDescription{
      flex-direction: column;
    }
    .map, .communityImage{
      width: 50vw;
    }
  }

  .communityImage{
    width: 40vw;
    margin: auto;
  }

  h2{
    font-size: 5em;
    font-weight: 600;
  }

  .map{
    width: 30vw;
    margin: auto;
  }

  .unitDescriptions{
    width: 90vw;
    margin: auto;
  }
